// COMPONENTS
.page-wrap {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

// All of the outermost items are absolutely positioned
.site-header {
  z-index: 10;
  @include mobile-only {
    padding-left: $h_pad_mobile;
    padding-right: $h_pad_mobile;
    display: flex;
    flex-wrap: wrap;
  }

  @include desktop-only {
      max-width: $cp_width--large;
      left: $h_pad_desktop;
  }

  @include xl-only {
    max-width: $cp_width--xl;
  }

  .site-title {
    @extend %bold;
    font-size: $font_size_2;
    padding-top: calcRem(1,$f1);
    padding-bottom: calcRem(1,$f1);
    margin-bottom: calcRem(1,$f1);

    @include mobile-only {
      border: none;
      max-width: calcVW(12,$cols);
    }
    @include desktop-only {
      border-bottom: 2px solid;
      @include gray-border;
    }
  }
  .chart-header {
    padding-bottom: calcRem(1,$f1);
    @include mobile-only {
      order: 2;
      margin-top: calcRem($f2,1);
    }
  }
  .chart-header__heading {
    font-size: $font_size_2;
  }

  .chart-header__subheading {
    font-size: $font_size_1;
  }

  .chart-header__label {
    @extend %gray-text;
    font-size: $font_size_1;
  }

  // .fine-print-wrap {
  //   @extend %flex-wrap;
  //   @extend %flex-justify-between;
  //   @include mobile-only {
  //     width: 100%;
  //   }
  //   @include desktop-only {
  //     width: calcVW(3,$cols);
  //     margin-top: calcRem($f2,1);
  //     padding-bottom:  calcRem($f1,0.25); // to match up borders
  //   }
  //
  // }
  //
  // .fine-print {
  //   font-size: $font_size_0;
  //   line-height: $font_size_1;
  //
  //   a {
  //     text-transform: uppercase;
  //   }
  // }

  .toggle-control-panel {
    box-shadow: none;
    outline: none;
    overflow: visible;

    @include mobile-only {
      padding-right: calcRem($f1,4);
      position: absolute;
      top: 0;
      right: 0;

      .icon--from_left {
        display: none;
      }
    }

    @include desktop-only {
      display: inline-block;
      text-align: right;
      text-transform: none;
      margin-top: calcRem(1,$f1);
      width: calcVW(3,$cols);
      border-top: 2px solid;
      padding-top: $font_size_1;
      padding-right: calcRem($f1,5);

      .icon--from_left {
        position: absolute;
        top: 7px;
        right: 1rem;
      }
    }
    @include xl-only {
      min-width: calcRem($f1,12);
      width: calcVW(2,$cols);
    }

    .text--slideout-primary,
    .text--slideout-secondary {
      @media(max-width: 767px) {
        display: none;
      }
    }

    .icon--from_right {
      position: absolute;
      top: 7px;
      left: 0;
    }


  }
}

.theme-toggle {
  width: 8rem;
  margin-top: calcRem(1,$f1);
  margin-bottom: calcRem(2,$f1);
  right: 0;

  .theme-toggle__option {
    width: 50%;
    display: block;
    position: absolute;
    top: 0;
    &.second-option {
      left: 50%;
    }
  }
  .theme-toggle__input {
    opacity: 0;
    border: 0;
    transition: opacity 0.2s ease-in-out;
  }

  .theme-toggle__label {
    // padding: 0.5rem 2rem;
    display: block;
    width: 100%;
    position: absolute;
    top: 0;
    text-align: center;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    font-size: $font_size_0;
    @extend %uppercase;
    &:hover {
      @include desktop-only {
        text-decoration: underline;
      }
    }
  }
  .theme-toggle__label:before {
    border: 2px solid;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .theme-toggle--left {
    left: 0;
    top: 0;
    @extend %light-background;
  }

  .theme-toggle--left,
  .theme-toggle--left:before {
    border-radius: 8px 0 0 8px;
  }

  .theme-toggle--right{
    right: 0;
    top: 0;
    @extend %dark-background;
  }

  .theme-toggle--right,
  .theme-toggle--right:before {
    border-radius: 0 8px 8px 0;
  }
  // don't put the dot in these sections
  input[type=radio] + .theme-toggle__label:after,
  input[type=radio]:checked + .theme-toggle__label:after {
    display: none;
  }

  input[type=radio]:checked + .theme-toggle__label.theme-toggle--left {
    @extend %black-background;

    @extend %white-text;
  }
  input[type=radio]:checked + .theme-toggle__label.theme-toggle--left:before {
    // @extend %white-border;
  }

  input[type=radio]:checked + .theme-toggle__label.theme-toggle--right:before {
    // @extend %black-border;
  }


  input[type=radio]:checked + .theme-toggle__label.theme-toggle--right {
    @extend %white-background;
    @extend %bright-red-border;
    @extend %black-text;
  }

}

