  .toggle-option--right,
  .toggle-option--right:before {
    border-radius: 0 8px 8px 0;
  }


  // don't put the dot in these sections
  input[type=radio] + .toggle-option__label:after,
  input[type=radio]:checked + .toggle-option__label:after {
    display: none;
  }

  .button--cta {
    @include mobile-only {
      position: absolute;
      top: calcRem($f1,3);
    }
  }

}

