.details-panel {
  @include desktop-only {
    height: 100%;
    min-height: 100vh;
    overflow-y: scroll;
  }
  position: relative;

  .button--close {
    position: fixed;
    z-index: 12;
    right: calcVW(1,$grid);
  }

  .button--icon {
    border: none;
    width: 36px;
    height: 36px
  }

  .icon-text {
    display: none;
  }

  // .icon--close {
  //   display: none;
  // }

  .icon--close {
    display: block;
    border: 2px solid;
    width: 36px;
    height: 36px;
    position: absolute;
    top: 0;
    right: 0;
  }




  .details-panel__fade {
    height: 100%;
    padding-top: calcRem(6,$f2);
    // padding-left: calcVW(1,$grid);
    // padding-right: calcVW(1,$grid);
    position: fixed;
    overflow-y: scroll;
    will-change: scroll-position;
    width: 100%;
  }

  .details-panel__fade::before,
  .details-panel__fade::after {
    content: "";
    height: calcRem(6,$f2);
    position: fixed;
    width: 100%;
    z-index: 1;
  }
  .details-panel__fade::before {
    top: 0;
    position: fixed;
  }

  .details-panel__fade::after {
    bottom: 0;
    position: fixed;
  }

  .details-panel__scroll {
    padding-bottom: calcVH(16,$grid);
  }

  .details-panel__heading,
  .details-panel__text {
    padding-left: calcVW(1,$grid);
    padding-right: calcVW(1,$grid);
  }

  .details-panel__heading {
    @extend %bold;
  }

  .details-panel__text {
    font-size: $font_size_1;
    margin-bottom: $font_size_1;
  }

  .text--table-details {
    font-size: $font_size_0;
    margin-bottom: $font_size_0;
    line-height: 1.4;

    &.is_long_url {
      hyphens: auto;
      overflow-wrap: break-word;
      word-break: break-all;
    }
  }

  @include mobile-only {
    // width: $cp_width--mobile; see _animated.scss
    padding: $h_pad_mobile;
    padding-bottom: calcRem(10,$f1);
  }
  @include desktop-only {
    // width: $cp_width--large; // see _animated.scss
    padding-bottom: calcRem(10,$f1);
  }
  @include xl-only {
    // width: $cp_width--xl;
  }
}


